#cities {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 255px;
  min-height: 45px;
  cursor: pointer; }
  #cities select {
    display: none;
    opacity: 0; }
  #cities.active .city__selected::after {
    transform: translate(-100%, -50%) rotate(180deg); }

.city {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0; }
  .city span {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 255px;
    min-height: 45px;
    height: 100%;
    padding: 0 15px;
    font-family: "Century Gothic", "Arial", sans-serif;
    font-weight: 700;
    font-style: italic;
    box-sizing: border-box;
    transition: color .5s ease;
    cursor: pointer; }
    .city span:hover {
      color: #f36421; }
  .city__selected {
    position: relative;
    z-index: 2;
    background-color: #ffffff;
    border-radius: 5px; }
    .city__selected::before, .city__selected::after {
      position: absolute;
      top: 50%;
      font-family: 'Flaticon';
      font-weight: 600;
      font-style: normal;
      z-index: 2;
      color: #f36421; }
    .city__selected::before {
      content: '\f103';
      left: 15px;
      font-size: 20px;
      transform: translateY(-50%); }
    .city__selected::after {
      content: '\f101';
      left: calc(100% - 15px);
      font-size: 15px;
      transform: translate(-100%, -50%);
      transition: transform .5s ease; }
  .city__wrapper {
    display: none;
    position: relative;
    z-index: 1;
    top: -25px;
    padding-top: 25px;
    background-color: #ffffff;
    border-radius: 5px; }
  .city__item {
    opacity: 0;
    transition: opacity .5s ease; }
    .city__item.show {
      opacity: 1; }
    .city__item.hidden {
      display: none !important;
      opacity: 0 !important; }

.mobile-menu {
  position: relative;
  z-index: 999;
  display: none;
  width: 50px;
  height: 30px;
  cursor: pointer; }
  @media only screen and (max-width: 1500px) {
    .mobile-menu {
      display: flex;
      flex-wrap: nowrap;
      flex-direction: column;
      justify-content: center;
      align-items: center; } }
  .mobile-menu span {
    position: absolute;
    left: 0;
    right: 0;
    display: block;
    width: 100%;
    height: 5px;
    border-radius: 30px;
    background-color: #f36421;
    transition: all .5s ease; }
    .mobile-menu span:nth-child(1) {
      top: 0; }
    .mobile-menu span:nth-child(2) {
      top: 50%; }
    .mobile-menu span:nth-child(3) {
      top: 50%; }
    .mobile-menu span:nth-child(4) {
      top: 100%; }
  .mobile-menu.active span:nth-child(1) {
    top: 50%;
    right: 50%;
    left: 50%;
    width: 0; }
  .mobile-menu.active span:nth-child(2) {
    transform: rotate(45deg); }
  .mobile-menu.active span:nth-child(3) {
    transform: rotate(-45deg); }
  .mobile-menu.active span:nth-child(4) {
    top: 50%;
    right: 50%;
    left: 50%;
    width: 0; }

.bg-header {
